import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";

import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import NavBarInitialDefault from "components/NavBarInitialDefault";
import bgImage from "assets/images/bg-presentation.jpg";
import DefaultFooter from "examples/Footers/DefaultFooter";
import footerRoutes from "footer.routes";

export default function PrivacyPolicy() {
  return (
    <>
      <NavBarInitialDefault />
      <MKBox
        minHeight="75vh"
        width="100%"
        sx={{
          backgroundImage: `url(${bgImage})`,
          backgroundSize: "cover",
          backgroundPosition: "top",
          display: "grid",
          placeItems: "center",
        }}
      >
        <Container>
          <Grid container item xs={12} lg={7} justifyContent="center" mx="auto">
            <MKTypography
              variant="h1"
              color="white"
              mt={-6}
              mb={1}
              sx={({ breakpoints, typography: { size } }) => ({
                [breakpoints.down("md")]: {
                  fontSize: size["3xl"],
                },
              })}
            >
              Politique De Confidentialit
            </MKTypography>
          </Grid>
        </Container>
      </MKBox>
      <Container>
        <div style={{ paddingTop: "2rem", paddingBottom: "2rem" }}>
          <p>
            Bienvenue sur notre site web ! Nous accordons une grande importance  la protection de
            vos donnes personnelles. Cette politique de confidentialit vise  vous informer sur la
            manire dont nous collectons, utilisons, partageons et stockons vos donnes.
          </p>
          <p>
            <strong>Collecte des donnes :</strong> Nous collectons certaines donnes personnelles
            lorsque vous utilisez notre site web, telles que votre nom, adresse e-mail et
            informations de contact. Ces donnes sont collectes lorsque vous remplissez des
            formulaires, vous inscrivez  notre newsletter ou interagissez avec notre site.
          </p>
          <p>
            <strong>Utilisation des donnes :</strong> Les donnes que nous collectons sont
            utilises pour vous fournir les services que vous demandez, amliorer notre site web et
            personnaliser votre exprience. Nous pouvons galement utiliser vos donnes pour vous
            envoyer des informations sur nos produits, offres spciales ou mises  jour du site,
            mais nous ne le ferons qu&apos;avec votre consentement.
          </p>
          <p>
            <strong>Partage des donnes :</strong> Nous ne partagerons pas vos donnes personnelles
            avec des tiers sans votre consentement, sauf dans les cas o cela est requis par la loi
            ou pour protger nos droits, notre scurit ou celle d&apos;autrui.
          </p>
          <p>
            <strong>Stockage des donnes :</strong> Vos donnes personnelles seront conserves aussi
            longtemps que ncessaire pour atteindre les fins pour lesquelles elles ont t
            collectes et conformment  la loi en vigueur.
          </p>
          <p>
            <strong>Protection des donnes :</strong> Nous prenons des mesures de scurit
            appropries pour protger vos donnes personnelles contre tout accs non autoris ou
            toute divulgation. Cependant, veuillez noter qu&apos;aucune mthode de transmission sur
            Internet, ou mthode de stockage lectronique, n&apos;est totalement scurise.
          </p>
          <p>
            <strong>Vos droits :</strong> Vous avez le droit de demander l&apos;accs, la
            rectification, la suppression ou la limitation du traitement de vos donnes
            personnelles. Vous avez galement le droit de vous opposer au traitement de vos donnes
             des fins de marketing direct. Pour exercer ces droits, veuillez nous contacter 
            l&apos;adresse e-mail fournie ci-dessous.
          </p>
          <p>
            Si vous avez des questions concernant notre politique de confidentialit, veuillez nous
            contacter  l&apos;adresse e-mail suivante : gouvernance@exemple.com.
          </p>
        </div>
      </Container>
      <MKBox pt={6} px={1} mt={6}>
        <DefaultFooter content={footerRoutes} />
      </MKBox>
    </>
  );
}
